import { useEffect, useState } from 'react'

import {
  Card,
  CardBody,
  Table,  
  CardTitle,  
  Badge
} from 'reactstrap'

import '@styles/react/libs/flatpickr/flatpickr.scss'
import axios from '@src/configs/axios/axiosConfig'

import moment from 'moment'
import { useParams } from 'react-router-dom'

const DocumentinOut = ({  }) => {

  const [data, setData] = useState([])
  const { id } = useParams()
  const statusArr = ['', 'In', 'Out']
  const statusColArr = ['', 'light-primary', 'light-success']

  const getLicenses = () => {
    axios.post('/documentsinout/list', { clientId: id })
      .then((res) => { setData(res.data.documentsinout.documentsinout) })
      .catch((err) => { console.log(err) })
  }

  const dateFormat = (value) => {
    if (value === null || value === undefined) {
      return ''
    }
    if (value.length === 13) {
      return moment(value, 'x').format('MMM DD, YYYY')
    } else {
      return moment.unix(value).format('MMM DD, YYYY')
    }
  }

  useEffect(() => {    
      getLicenses()
    
  }, [])

   return (
    <Card>
      <CardBody >
        <CardTitle className='align-items-end card-title d-flex justify-content-between mb-0' tag='h5'>
          Document In - Out          
        </CardTitle>
      </CardBody>
      <Table className='text-nowrap text-center border-bottom' responsive>
        <thead>
          <tr>
            <th className='text-start'>Name</th>
            <th>Name of the Person</th>
            <th>Date</th>
            <th>Status</th>
            <th>Remarks</th>           
          </tr>
        </thead>
        <tbody>
          {data.map((item, index) => {
            return (
              <tr key={index}>
                <td className='text-start'>{item.name}</td>
                <td>
                  {item.nameofperson}
                </td>
                <td>
                  {item.date !== undefined ? dateFormat(item.date) : ''}
                </td>
                <td>
                  <Badge color={statusColArr[item.documentstatus]}> {statusArr[item.documentstatus]}</Badge>
                </td>
                <td className='text-start'>
                  <span className='text-start'> {item.remarks} </span>
                </td>             
              </tr>
            )
          })}
        </tbody>
      </Table>
   
    </Card>
  )
}

export default DocumentinOut
